import React from "react";
import { Link } from "react-scroll";
import Pagetitle from "../elements/Pagetitle";
import Service from "../elements/Service";

const servicesData = [
  {
    id: 1,
    icon: "images/service-1.svg",
    title: "Frontend",
    content:
      "I possess a diverse skill set in front-end development, including React.js, Next.js, jQuery, and React Native, as well as proficiency in popular CSS frameworks such as Bootstrap, Mui, and Ant Design. My expertise enables me to create dynamic and visually appealing user interfaces for both web and mobile applications, while prioritizing the delivery of engaging and seamless user experiences.",
    color: "#6C6CE5",
    contentColor: "light",
  },
  {
    id: 2,
    icon: "images/service-2.svg",
    title: "Backend",
    content:
      "Regarding back-end development, I have worked extensively with PHP and Express.js. However, I find that Express.js with GraphQL provides better performance outcomes than PHP. Thus, I prefer utilizing Express.js (GraphQL) for my projects to create robust and efficient back-end systems that enhance the overall functionality and user experience of web and mobile applications.",
    color: "#F9D74C",
    contentColor: "dark",
  },
  {
    id: 3,
    icon: "images/service-3.svg",
    title: "Database Design",
    content:
      "As a developer, I am skilled in both MySQL and MongoDB databases, selecting the appropriate technology based on the project's requirements and the backend language used. I prioritize efficiency and scalability when developing database structures to support web and mobile applications.",
    color: "rgb(242 76 97)",
    contentColor: "light",
  },
];

function Services() {
  return (
    <section id="services">
      <div className="container">
        <Pagetitle title="Skillset" />
        <div className="row fix-spacing">
          {servicesData.map((service) => (
            <div className="col-md-4" key={service.id}>
              <Service service={service} />
            </div>
          ))}
        </div>
        <div className="mt-5 text-center">
          <p className="mb-0">
            Looking for a custom job?{" "}
            <a
              className="colorpink pointer"
              href="https://api.whatsapp.com/send?phone=60163211034" target="_blank"
            >
              Click here
            </a>{" "}
            to contact me! 👋
          </p>
        </div>
      </div>
    </section>
  );
}

export default Services;
