import React from "react";
import Pagetitle from "../elements/Pagetitle";
import Timeline from "../elements/Timeline";

const educationData = [
  {
    id: 1,
    title: "Diploma in Software Engineering",
    years: "2019 - 2020",
    content:
      "I hold a software engineering diploma from Asia Pacific University, and I utilize my knowledge and skills to develop high-quality software solutions that meet the diverse needs of my clients.",
  }
];

const experienceData = [
  {
    id: 1,
    title: "Full Stack Developer",
    years: "2020 - 2021",
    content:
      "At Antforce Sdn Bhd, I utilized my skills in Jquery and PHP as a full-stack developer to develop innovative and scalable software projects. I worked closely with cross-functional teams to gather requirements, design and implement software solutions, and ensure the quality and reliability of the final product.",
  },
  {
    id: 2,
    title: "Mern Stack Developer",
    years: "2021 - Present",
    content:
      "I'm currently a full-stack developer at Overwatch Technologies Sdn Bhd, specializing in React.js, React Native, and Express.js (GraphQL). My responsibilities include designing and implementing software solutions, performing extensive testing, and staying up-to-date with the latest technologies to deliver high-quality results.",
  }
];

function Experiences() {
  return (
    <section id="experience">
      <div className="container">
        <Pagetitle title="Experience" />
        <div className="row">
          <div className="col-md-6">
            <div className="timeline edu bg-white rounded shadow-dark padding-30 overflow-hidden">
              {educationData.map((education) => (
                <Timeline key={education.id} education={education} />
              ))}
              <span className="line"></span>
            </div>
          </div>

          <div className="col-md-6">
            <div className="spacer d-md-none d-lg-none" data-height="30"></div>
            <div className="timeline exp bg-white rounded shadow-dark padding-30 overflow-hidden">
              {experienceData.map((experience) => (
                <Timeline key={experience.id} education={experience} />
              ))}
              <span className="line"></span>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Experiences;
